import { BaseValueObject } from '../base';

export const WORK_STATUS_LIST = [
  'NOT_STARTED',
  'DESIGN_IN_PROGRESS',
  'START_AT_NEXT_MONTH',
  'IN_PROGRESS_AT_SITE',
  'CONSTRUCTION_COMPLETE',
  'MONTHLY_REPORT_COMPLETE',
  'PAYMENT_RECEIVED',
  'ABORTED',
  'DELETED',
] as const;

export type WORK_STATUS_TYPE = (typeof WORK_STATUS_LIST)[number];
export const CONTRACT_STATUS_LIST = ['NEW', 'SIGNED', 'CANCELED'] as const;
export type CONTRACT_STATUS_TYPE = (typeof CONTRACT_STATUS_LIST)[number];

export class WorkStatus extends BaseValueObject<'WorkStatus', string> {
  static from(value: string): WorkStatus {
    return new WorkStatus(value);
  }

  constructor(value: string) {
    super(value);
  }

  static makeNotStartedStatus(): WorkStatus {
    return new WorkStatus('NOT_STARTED');
  }

  static makeDeletedStatus(): WorkStatus {
    return new WorkStatus('DELETED');
  }

  protected validate(value: string): string {
    if (!WORK_STATUS_LIST.includes(value as WORK_STATUS_TYPE)) {
      throw new Error('invalid work status');
    }

    return value;
  }
}

export class ContractStatus extends BaseValueObject<
  'ContractStatuses',
  string
> {
  static from(value: string): ContractStatus {
    return new ContractStatus(value);
  }

  constructor(value: string) {
    super(value);
  }

  static makeNewStatus(): ContractStatus {
    return new ContractStatus('NEW');
  }

  protected validate(value: string): string {
    if (!CONTRACT_STATUS_LIST.includes(value as CONTRACT_STATUS_TYPE)) {
      throw new Error('invalid contract status');
    }
    return value;
  }
}
